<template>
  <span>
    <el-popover
      v-if="typeof character === 'object'"
      placement="right"
      width="280"
      ref="charCard"
      trigger="click"
      @show="createAnimation"
      @hide="destroyAnimation"
    >
<!--      <p class="character" :id="`character-${sentenceIndex}-${characterIndex}`">{{ character['s'] }}{{ character['s'] === character['t'] ? '' : ` / ${character['t']}` }}</p>-->
      <div v-if="animateChar" class="character mb-3">
        <span v-for="(character, index) in charCard" :key="index" :id="`${tab}-${sentenceIndex}-${characterIndex}-${character}-${index}`"></span>
        <div v-html="charCard"></div>
      </div>
      <p class="pinyin text-left" v-html="pinyin"></p>
      <p class="english text-left" v-html="english"></p>
      <span class="transcript align-middle" slot="reference" v-html="typeof character === 'object' ? charOutput : character"></span>
    </el-popover>
    <span v-else >{{character}}</span>
  </span>
</template>

<script>
  import convertPinyinTones from 'pinyin-tone-converter'; //QUIZLET
  import HanziWriter from 'hanzi-writer'

  export default {
    name: "CharacterItem",
    props: {
      character: {
        type: Object | String
      },
      type: {
        type: String,
        default: 'simplified'
      },
      popup: {
        type: Boolean,
        default: false
      },
      characterIndex: Number,
      sentenceIndex: Number,
      tab: String,
    },
    data() {
      return {
        animateChar: true
      }
    },
    computed: {
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      charCard() {
        return typeof this.character === 'object' ? this.character[this.charSet.slice(0,1)] : this.character
      },
      charOutput () {
        switch (this.type) {
          case 'simplified':
            return this.character['s'];
          case 'traditional':
            return this.character['t'];
          case 'pinyin':
            return ' ' + convertPinyinTones(this.character['p']);
          default:
            return this.character['s'];
        }
      },
      pinyin () {
        return typeof this.character === 'object' ? convertPinyinTones(this.character['p']).toString() : '';
      },
      english () {
        return this.character.en.split(`\\&#039;`).join('\'')
      }
    },
    methods: {
      async createAnimation () {
        this.animateChar = true;
        if (this.charCard.length <= 4) {
          console.log('Open CharCard');
          setTimeout(() => {
            this.charCard.split('').forEach((character, index) => {
              console.log(`--${character}--`);
              console.log(`${this.tab}-${this.sentenceIndex}-${this.characterIndex}-${character}-${index}`);
              HanziWriter.create(`${this.tab}-${this.sentenceIndex}-${this.characterIndex}-${character}-${index}`, character, {
                // charDataLoader: function (char) {return require(`hanzi-writer-data/${char}`)},
                width: 60,
                height: 60,
                padding: 0,
                delayBetweenLoops: 3000
              }).animateCharacter();
            })
          }, 0);
        }
      },
      destroyAnimation () {
        this.animateChar = false;
        console.log('Close CharCard')
      },
    }
  }
</script>

<style lang="scss" scoped>
  .character {
    font-size: 45px;
  }
  .pinyin, .english {
    font-size: 24px;
  }
  .transcript {
    &:hover {
      border-radius: 10%;
      background-color: #fff;
      box-shadow: 0 0 5px 2px #E1001E;
    }
  }
  .emphasize-dark {
    text-shadow: 0 0 5px black;
  }
</style>
